import axios from "axios";
import Vue from "vue";
import qs from "qs";
import { Toast } from "vant";
import router from "@/core/common/router";
import { parseLoactionSearch } from "../utils/utils";
Vue.use(Toast);

const { VUE_APP_DOMAIN } = process.env;
const domain = VUE_APP_DOMAIN;
const codeMessage = {
  200: "",
  201: "",
  202: "",
  204: "",
  400: ",",
  401: "",
  403: "",
  404: "",
  406: "",
  410: "",
  422: "",
  500: "",
  502: "",
  503: "",
  504: ""
};

function checkStatus(response) {
  const { status, statusText } = response;
  if (status > 300) {
    const errortext = codeMessage[status] || statusText;
    console.log("error");
  }

  if (status <= 504 && status >= 500) {
    router.push({
      path: "/exception/500"
    });
    return;
  }
  if (status >= 404 && status < 422) {
    router.push({
      path: "/exception/404"
    });
    return;
  }
  if (status === 401) {
    router.push({
      path: "/user/login"
    });
  }
}
export default function request(url, options = {}) {
  const defaultOptions = {
    withCredentials: true
  };
  const code = parseLoactionSearch() ;
  console.log("code", code);

  if (!options.method || (options.method && options.method.toLocaleUpperCase() === "GET")) {
    const { params = {} } = options;
    Object.assign(options, {
      params: { ...params, ...code }
    });
  } else if (!options.method || (options.method && options.method.toLocaleUpperCase() === "POST")) {
    const { data = {}, headers = {} } = options;
    const formaterData = { ...data, ...code };
    Object.assign(options, {
      data: qs.stringify(formaterData),
      headers: { ...headers, "Content-Type": "application/x-www-form-urlencoded" }
    });
  }

  const newOptions = {
    ...defaultOptions,
    ...options
  };
  return axios({
    url: `api${url}`,
    baseURL: domain,
    ...newOptions
  })
    .then((response = {}) => {
      const { data = {} } = response;
      const { code, msg } = data;
      if (code !== 100) {
        if (code === 401) {
          router.push({
            path: "/user/login"
          });
        } else {
          Toast.fail(msg);
        }
      }
      return data;
    })
    .catch(e => {
      if (e.response) {
        checkStatus(e.response);
      }
    });
}
